.form-container{
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.form-container form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin:  auto;
}
.form-container input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;
}
.form-container textarea{
    height: 5rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;
}
.form-container button{
    display: inline;
    height: 3rem;
    background-color: #2a2a2a;
    color: white;
    border: none;
    border-radius: .3rem;
    cursor: pointer;
}
@media screen and (max-width: 768px) {
    .form-container{
        margin: 4rem 2rem;
    }
    .form-container form{
        padding: 2rem;
        width: 90%;
    }
    
    
}